<template>
  <div class="dashboard">
    <span v-if="isShow" @click="closeNavbar" class="navBar">
      <img src="./img/弹性伸缩.svg" alt="" style="width: 100%; height: 100%" />
    </span>
    <span v-else @click="closeNavbar" class="navBar">
      <img
        src="./img/弹性伸缩.svg"
        alt=""
        style="width: 100%; height: 100%; transform: rotateY(180deg)"
    /></span>
  </div>
</template>

<script>
export default {
  name: "Head",
  data() {
    return {
      isShow: true,
    };
  },
  mounted() {},
  methods: {
    closeNavbar() {
      //自定义事件让navbar伸缩
      this.isShow = !this.isShow;
      this.$emit("closeNavbar", this.isShow);
    },
  },
};
</script>

<style scoped>
.dashboard {
  width: 99.9%;
  height: 99.8%;
  border: 1px solid white;
  box-shadow: 1px 1px 1px 1px gray;
  position: relative;
}
.navBar {
  display: inline-block;

  width: 30px;
  height: 30px;
  top: 0;

  left: 0;

  bottom: 0;

  margin: auto 5px;
  position: absolute;
}
</style>