<template>
  <div class="common-layout">
    <el-container>
      <el-aside class="bar" width="auto">
        <el-menu
          :default-active="active"
          active-text-color="#ffd04b"
          background-color="#545c64"
          text-color="#fff"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          :collapse="isCollapse"
          router
        >
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">导航一</span>
            </template>
            <el-menu-item-group>
              <el-menu-item
                :index="item.path"
                v-for="item in list"
                :key="item.path"
                >{{ item.meta.title }}</el-menu-item
              >
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header height="50px">
          <Head @closeNavbar="closeNavbar"></Head>
        </el-header>
        <el-main class="main"> <router-view></router-view></el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      isCollapse: true,
      active: "",
    };
  },
  mounted() {
    this.list = this.$router.getRoutes().filter((item) => {
      return item.meta.isShow;
    });

    this.active = this.list.path;
  },

  methods: {
    closeNavbar(pramas) {
      this.isCollapse = pramas;
    },
  },
};
</script>

<style scoped lang="less">
.common-layout {
  width: 100%;
  .el-header {
    margin: 0;
    padding: 0;
  }
  .bar {
    overflow: hidden;
    .el-menu {
      height: 100vh;
    }
  }
  .main {
    height: calc(100vh - 55px);
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 160px;
    min-height: 400px;
  }
}
</style>