<template>
  <div class="login-main">
     <img src="../assets/img/wave.png" alt="" class="wave"></img>
    <div class="img">
      <img src="../assets/img/bg.svg" alt="" />
    </div>
    <div class="form">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <h2 align="center">后台管理系统登录</h2>
      <div class="form-head">
        <img src="../assets/img/avatar.svg" alt="">
      </div>

        <el-form-item prop="username" class="svg">
          <el-input
            v-model="ruleForm.username"
            autocomplete="off"
            placeholder="请输入账号"
            type="text"
            class="input"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password" class="svg">
          <el-input
          
            type="password"
            v-model="ruleForm.password"
            autocomplete="off"
            placeholder="请输入密码"
            class="input"
          ></el-input>
        </el-form-item>

        <el-form-item class="btnclass">
          <el-button
            type="info"
            @click="submitForm('ruleForm')"
           class="btn"
            >登录</el-button
          >
         <p class="monster">没有账号？请联系管理员！</p>
        </el-form-item>

      </el-form>
  
    </div>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入账号"));
      } else if (value.length < 5) {
        callback(new Error("输入的账号过短或没有此用户"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [{ validator: validatePass, trigger: "blur" }],
        password: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        // if (valid) {
        //   alert("submit!");
        // } else {
        //   console.log("error submit!!");
        //   return false;
        // }

        this.$router.push("/main");
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    btn() {
      alert(123);
    },
  },
};
</script>

<style scoped lang="less" >
.login-main {
  margin: 0;
  display: flex;
  width: 100%;
  height: 100%;

  justify-content: space-between;
}

.form {
  width: 50%;
  height: 100%;
  display: flex;
  margin: 0;
  margin-right: 5%;
}

.form-head {
  width: 30%;
  height: 25%;

  margin-bottom: 50px;
  margin: 0 auto;
  border-radius: 50%;
}

.form-head img {
  width: 100%;
  height: 100%;
}
.demo-ruleForm {
  width: 300px;
  height: 50%;
  border: 1px solid white;
  border-radius: 10px;
  box-shadow: 1px 1px 2px 2px gray;
  align-self: center;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.5);
}
.wave {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 100%;
  /* 让他当作一个背景 */
  z-index: -1;
}
.svg {
  width: 80%;
  margin: 20px auto;
}

.img {
  display: flex;
  justify-content: flex-end;
  align-items: center;

  width: 45%;
}

.img img {
  width: 600px;
}

.monster {
  width: 100%;
  color: gray;
  font-family: "宋体";
  font-size: 10px;
  margin-top: 5px;
  text-align: center;
  opacity: 0.5;
}

.btnclass {
  width: 100%;
  height: 200px;

  .btn {
    display: block;
    width: 30%;
    margin: 0 auto;
  }
}
</style>
