import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '../view/login.vue'
import main from '../view/MainHome/index.vue'
Vue.use(VueRouter)


const router = new VueRouter({
    routes: [
        {
            path: "/",
            name: "login",
            component: login,


        },
        {
            path: '/main',
            name: "main",
            component: main,

            children: [
                {
                    path: 'child',
                    name: "child",

                    component: () => import('../view/MainHome/Child/index.vue'),
                    meta: {
                        title: "子组件",
                        isShow: true

                    }
                },
                {
                    path: "child2",
                    name: "child",
                    component: () => import('../view/MainHome/Child2/index.vue'),
                    meta: {
                        title: "2子组件",
                        isShow: true

                    }
                }
            ]

        }




    ]
})

router.beforeEach((to, from, next) => {
    to.meta.title && (document.title = to.meta.title);
    next()
});


export default router